import React from "react";
import { Image } from "react-native";

const Bird = ({ birdBottom, birdLeft }) => {
  const birdWidth = 80;
  const birdHeight = 60;

  return (
    <Image
      source={require("../assets/TheBird.png")} 
      style={{
        position: "absolute",
        width: birdWidth,
        height: birdHeight,
        left: birdLeft - birdWidth / 2,
        bottom: birdBottom - birdHeight / 2,
      }}
    />
  );
};

export default Bird;
